import React from 'react';

class Empty extends React.Component {
	render() {
		const props = this.props;
		const src = props.src || 'data:image/svg+xml;base64,';
		const title = props.title?props.title:'暂无数据';
		return (
			<div className="Empty rex-text-center" style={{margin: '14px auto'}}>
				<img src={src} width='122' height='100' alt='empty' />
				<div style={{margin: '4px auto'}}>{title}</div>
			</div>
		);
	}
}

export default Empty;