export const $http = {
	get: function (apiURL, params = {}) {
		const userinfo = window.JSON.stringify(LocalStorage.getObject('xy_userinfo')) === '{}'?{token: 'token not found'}:LocalStorage.getObject('xy_userinfo');
		return axios.get(apiURL, {
			headers: {
				'Http-Authorization': userinfo.token || 'token not found',
			},
			params
		});
	},
	post: function (apiURL, data = {}) {
		const userinfo = window.JSON.stringify(LocalStorage.getObject('xy_userinfo')) === '{}'?{token: 'token not found'}:LocalStorage.getObject('xy_userinfo');
		return axios.post(apiURL, transformRequest(data), {
			headers: {
				'Http-Authorization': userinfo.token || 'token not found',
			}
		});
	}
};

